  save() {
    if (this.appStoreService.loading) return;
    if (this.appStoreService.pId == 0) {
      return alert('Invalid Patient');
    }
    this.appStoreService.loading = true;
    try {
      this.http
        .doPost(
          `nurseshiftsummary/${!this.nurseShiftSummaryService.isUpdate
            ? 'save'
            : `update/${this.nurseShiftSummaryService.currentSysKey}`
          }`,
          {

            userid: this.appStoreService.userId,
            pId: this.appStoreService.pId,
            rgsNo: this.appStoreService.rgsNo,
            dayNight: this.dayNight,
            doctorSysKey: this.appStoreService.doctor.syskey,
            t1: this.t1,
            t2: this.t2,
            t3: this.t3,
            t4: this.t4,
            t5: this.t5,
            t6: this.t6,
            t7: this.t7,
            t8: this.t8,
            t9: this.t9,
            t10: this.t10,
            n1: this.n1,
            n2: this.n2,
            n3: this.n3,
            n4: this.n4,
            n5: this.n5,
            n6: this.n6,
            n7: this.n7,
            n8: this.n8,
            n9: this.n9,
            n10: this.n10,
          }
        )
        .subscribe((data: any) => {
          this.appStoreService.loading = false;
          if (!this.nurseShiftSummaryService.isUpdate) {
            this.nurseShiftSummaryService.currentSysKey = data.syskey;
          }
          this.nurseShiftSummaryService.isUpdate = true;
        });
    } catch (err) {
      alert(err.message);
      this.appStoreService.loading = false;
    }
  }


  valide(): boolean {
    if (this.t2 == "") return false;
    if (this.t3 == "") return false;
    if (this.t4 == "") return false;
    if (this.t5 == "") return false;
    if (this.t6 == "") return false;
    if (this.t7 == "") return false;
    else return true;
  }


