  save() {
    if (this.appStoreService.loading) return;
    if (this.appStoreService.pId == 0) {
      return alert('Invalid Patient');
    }
    this.appStoreService.loading = true;
    try {
      this.http
        .doPost(
          `nursingcarerecord/${!this.nurseCareRecordService.isUpdate
            ? 'save'
            : `update/${this.nurseCareRecordService.currentSysKey}`
          }`,
          {

            userid: this.appStoreService.userId,
            pId: this.appStoreService.pId,
            rgsNo: this.appStoreService.rgsNo,
            t1: this.t1,
            t2: this.t2,
            t3: this.t3,
            t4: this.t4,
            t5: this.t5,
            t6: this.t6,
            t7: this.t7,
            t8: this.t8,
            t9: this.t9,
            t10: this.t10,
            t11: this.t11,
            t12: this.relationship1,
            t13: this.relationship2,
            t14: this.relationship3,
            t15: this.relationship4,
            phone1: this.phone1,
            phone2: this.phone2,
            phone3: this.phone3,
            phone4: this.phone4,
            phone5: this.phone5,
            phone6: this.phone6,
            phone7: this.phone7,
            phone8: this.phone8,
            phone9: this.phone9,
            phone10: this.phone10,
            phone11: this.phone11,
            phone12: this.phone12,
            phone13: this.phone13,
            phone14: this.phone14,
            phone15: this.phone15,
            phone16: this.phone16,
            n1: this.n1,
            n2: this.n2,
            n3: this.n3,
            n4: this.n4,
            n5: this.n5,
            n6: this.n6,
            n7: this.n7,
            n8: this.n8,
            n9: this.n9,
            n10: this.n10,
          }
        )
        .subscribe((data: any) => {
          this.appStoreService.loading = false;
          if (!this.nurseCareRecordService.isUpdate) {
            this.nurseCareRecordService.currentSysKey = data.syskey;
          }
          this.nurseCareRecordService.isUpdate = true;
        });
    } catch (err) {
      alert(err.message);
      this.appStoreService.loading = false;
    }
  }

