  formatDate(dateStr: string, format: string) {
    return moment(dateStr).format(format);
  }
  initPagination(data) {
    this.start = 0;
    this.end = this.perPage;
    if (data.length < this.perPage) {
      this.end = data.length;
    }
    this.calculateTotal(data);
  }
  calculateTotal(data) {
    this.totalPage = Math.ceil(data.length / this.perPage);
    this.total = data.length;
  }
  goToList({ syskey }: { syskey: number }) {
    this.NursingShiftSummaryService.currentSysKey = syskey;
    this.NursingShiftSummaryService.isUpdate = true;
    this.NursingShiftSummaryService.tabNo = 2;
  }
  handlePerPageChanged(perPage) {
    this.perPage = perPage;
    this.initPagination(this.NursingShiftSummaryService.instructions);
  }
  handleSkip(n: number) {
    switch (n) {
      case 1:
        if (this.page < this.totalPage) {
          this.page++;
          this.end = this.page * this.perPage;
          if (this.page == this.totalPage) {
            this.end =
              this.NursingShiftSummaryService.instructions.length - this.start;
          }
          this.start = (this.page - 1) * this.perPage;
        }
        break;
      case 2:
        if (this.page !== 1) {
          this.page--;
          this.end = this.page * this.perPage;
          this.start = (this.page - 1) * this.perPage;
        } else {
          this.start = (this.page - 1) * this.perPage;
          this.end = this.perPage;
          if (this.NursingShiftSummaryService.instructions.length < this.perPage) {
            this.end = this.NursingShiftSummaryService.instructions.length;
          }
        }
      case 3:
        this.page = 1;
        this.start = (this.page - 1) * this.perPage;
        this.end = this.perPage;
        if (this.NursingShiftSummaryService.instructions.length < this.perPage) {
          this.end = this.NursingShiftSummaryService.instructions.length;
        }
        break;
      default:
        this.page = this.totalPage;
        this.start = (this.page - 1) * this.perPage;
        if (
          this.NursingShiftSummaryService.instructions.length % this.perPage ===
          0
        ) {
          this.end = this.page * this.perPage;
        } else {
          this.end =
            this.start +
            (this.NursingShiftSummaryService.instructions.length % this.perPage);
        }
    }
  }
